.faq {
  &__banner {
    background: url(../../assert/images/faq-banner.png) no-repeat 50% 50%/auto 100%;
  }

  &__main {
    display: flex;
    padding-top: 60px;
    padding-bottom: 60px;
  }
  
  &__category {
    padding: 2px;
    
    &-item {
      position: relative;
      padding-top: 17.5px;
      padding-right: 17.5px;
      background: #FFFFFF;
      font-size:14px;
      font-weight:400;
      color:#666666;

      &:hover,
      &.active {
        font-weight:bold;
        color:#2661E9;
      }

      &.active {
        &:before {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
          display: inline-block;
          width:4px;
          height:18px;
          background:#2661E9;
        }
      }
    }
  }

  &__content {
    width: 100%;
    padding: 2px;
  }

  &__card {
    padding: 0 24px;
    border: none;
    border-radius: 0;

    & + & {
      margin-top: 4px;
    }

    &-header {
      display: flex;
      justify-content: space-between;
      padding: 16.5px 0;
      background: #FFFFFF;
      border: none;

      .iconfont {
        font-size: 10px;
        transform-origin: 50% 50%;
        transition: all .5s;

        &.reversed {
          transform: rotate(-180deg);
        }
      }
    }

    &-body {
      padding: 20px 0;
      border-top: 1px solid #CCCCCC;
    }
  }

  &__question {
    color: #333333;
    font-weight: bold;
  }
}
